.basket {
  padding: 7rem 0 0 0;

  &__heading {

  }
}

.cart {
  display: grid;
  gap: 10rem;
  grid-template:
    "products info" /
      4fr 2fr;

  &__products {
    grid-area: products;
  }

  &__info {
    grid-area: info;
  }

  .cart-product {
    display: grid;
    grid-template: "thumb info" / 1fr 1fr;
    gap: 4.5rem;

    &__thumb {
      grid-area: thumb;

      img {
        width: 100%;
        height: auto;
      }
    }

    &__info {
      grid-area: info;
      display: flex;
      gap: 3rem;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      height: 100%;
      align-content: flex-start;
    }

    &__quantity, &__delete, &__price {

    }

    &__quantity-handler {
      display: flex;
      gap: 2.5rem;
    }

    &__price, &__quantity {
      display: flex;
      align-items: center;
      gap: 1.5rem
    }
  }
}